.navbarContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
}

.logoSection a {
    color: white;
    text-decoration: none;
    font-size: 20px;
    font-weight: bold;
}
  
.aliasForm {
    display: flex;
    gap: 10px;
}
  
.aliasInput {
    padding: 5px;
    border-radius: 4px;
    border: 1px solid #ccc;
    outline: none;
}
  
.aliasButton {
    padding: 5px 10px;
    background-color: #0056b3;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    color: white;
}
  
.aliasButton:hover {
    background-color: #003580;
}
  
.aliasDisplay {
    margin-left: 20px;
}
  