.roomContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    max-width: 600px;
    height: 500px;
}

.roomTitle {
    margin-bottom: 20px;
}
  
.messagesContainer {
    width: 100%;
    overflow-y: scroll;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 4px;
}
  
.message {
    margin: 5px 0;
}
  
.messageSender {
    font-weight: bold;
}
  
.messageForm {
    display: flex;
    gap: 10px;
    width: 100%;
}
  
.messageInput {
    flex-grow: 1;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    outline: none;
}

.sendMessageButton {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    background-color: #007bff;
    color: white;
}
  
.sendMessageButton:hover {
    background-color: #0056b3;
}
  