.roomCreationContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}

.creationTitle {
    margin-bottom: 20px;
}

.creationForm {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    max-width: 400px;
}
  
.roomInput {
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    width: 100%;
    outline: none;
}

.createRoomButton {
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    background-color: #007bff;
    color: white;
    width: 100%;
}
  
.createRoomButton:hover {
    background-color: #0056b3;
}
